<script>
import projects from './Tables/projects'
import {Table, TableColumn} from 'element-ui'
import Users from './Tables/users'
import axios from "axios";
import {baseUrl} from "../config";

export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      menus:[],
      rows: 1,
      perPage: 1,
      currentPage: 1,
      index: 0,
      right_t: false,
      data_main: [],
      title:'',
      order:'',
      isFooter:false,
      url:'',
      isButton:true,
      main_id:''
    };
  },
  methods: {
    deletes(item) {
      let isBoss = confirm("You really want to delete?");
      if (isBoss == true) {
       axios.delete(`${baseUrl}menus/${item.id}/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          })
          .then(() => {
            this.$toast("Ma'lumot o'chirildi", {
              timeout: 2000,
              type:"success"
            });
            axios.get(`${baseUrl}menus/`)
              .then(res=>{
                this.menus=res.data

              })
          })
      }
    },
      rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    create(){
      axios.post(`${baseUrl}menus/`,{
        "title":this.title,
        "order":this.order,
        "url":this.url,
        "is_footer":this.isFooter
      },
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
      .then(()=>{
        axios.get(`${baseUrl}menus/`)
          .then(res=>{
            this.menus=res.data


          })
      })
    },
    edit(){
      axios.patch(`${baseUrl}menus/${this.main_id}/`,{
          "title":this.title,

        },
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
        .then(()=>{
          axios.get(`${baseUrl}menus/`)
            .then(res=>{
              this.menus=res.data


            })
        })
    },
    edit_main(row){
      this.main_id=row.id
      this.isButton=true
      this.isFooter=row.is_footer
      this.url=row.url
      this.order=row.order
      this.title=row.title
    }
  },
  mounted() {
    axios.get(`${baseUrl}menus/`)
    .then(res=>{
      this.menus=res.data
      console.log(res)
    })
  }
}

</script>
